"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Checkbox } from "@/components/ui/checkbox";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog";
import { Label } from "@/components/ui/label";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";

// Mock data for dashboard tasks
const pendingTasks = [
  { id: 1, title: "Review Q1 budget proposal", priority: "High", dueDate: "2024-07-20", assignee: "John Doe", status: "pending" },
  { id: 2, title: "Update website homepage design", priority: "Medium", dueDate: "2024-07-22", assignee: "Jane Smith", status: "pending" },
  { id: 3, title: "Prepare client presentation", priority: "High", dueDate: "2024-07-21", assignee: "Mike Johnson", status: "pending" },
  { id: 4, title: "Fix mobile app bugs", priority: "Low", dueDate: "2024-07-25", assignee: "Sarah Wilson", status: "pending" },
  { id: 5, title: "Schedule team meeting", priority: "Medium", dueDate: "2024-07-19", assignee: "Alex Brown", status: "pending" },
];

const completedTasks = [
  { id: 6, title: "Send weekly report", priority: "Medium", dueDate: "2024-07-18", assignee: "Emma Davis", status: "completed" },
  { id: 7, title: "Update documentation", priority: "Low", dueDate: "2024-07-17", assignee: "Chris Miller", status: "completed" },
];

const teamStats = {
  totalMembers: 12,
  activeProjects: 8,
  completedTasks: 24,
  pendingTasks: 5,
};

export default function DashboardPage() {
  const [tasks, setTasks] = useState([...pendingTasks, ...completedTasks]);
  const [showNewTaskDialog, setShowNewTaskDialog] = useState(false);
  const [newTask, setNewTask] = useState({
    title: "",
    description: "",
    priority: "Medium",
    assignee: "",
    dueDate: "",
  });

  const handleCreateTask = () => {
    const task = {
      id: tasks.length + 1,
      title: newTask.title,
      priority: newTask.priority,
      dueDate: newTask.dueDate,
      assignee: newTask.assignee,
      status: "pending",
    };
    setTasks([task, ...tasks]);
    setShowNewTaskDialog(false);
    setNewTask({ title: "", description: "", priority: "Medium", assignee: "", dueDate: "" });
  };

  const toggleTaskStatus = (taskId: number) => {
    setTasks(tasks.map(task => 
      task.id === taskId 
        ? { ...task, status: task.status === "pending" ? "completed" : "pending" }
        : task
    ));
  };

  const getPriorityColor = (priority: string) => {
    const colors = {
      High: "bg-red-100 text-red-800",
      Medium: "bg-yellow-100 text-yellow-800",
      Low: "bg-blue-100 text-blue-800",
    };
    return colors[priority as keyof typeof colors] || "bg-gray-100 text-gray-800";
  };

  return (
    <div className="flex-1 overflow-auto bg-gray-50">
      <div className="p-6">
        {/* Header */}
        <div className="flex items-center justify-between mb-6">
          <div>
            <h1 className="text-3xl font-bold text-gray-900">Dashboard</h1>
            <p className="text-gray-600 mt-1">Welcome back! Here's what's happening with your projects.</p>
          </div>
          <Dialog open={showNewTaskDialog} onOpenChange={setShowNewTaskDialog}>
            <DialogTrigger asChild>
              <Button className="bg-purple-600 hover:bg-purple-700">
                + Create New Task
              </Button>
            </DialogTrigger>
            <DialogContent className="sm:max-w-[425px]">
              <DialogHeader>
                <DialogTitle>Create New Task</DialogTitle>
                <DialogDescription>
                  Add a new task to your project. Fill in the details below.
                </DialogDescription>
              </DialogHeader>
              <div className="grid gap-4 py-4">
                <div className="grid gap-2">
                  <Label htmlFor="title">Task Title</Label>
                  <Input
                    id="title"
                    value={newTask.title}
                    onChange={(e) => setNewTask({ ...newTask, title: e.target.value })}
                    placeholder="Enter task title"
                  />
                </div>
                <div className="grid gap-2">
                  <Label htmlFor="description">Description</Label>
                  <Textarea
                    id="description"
                    value={newTask.description}
                    onChange={(e) => setNewTask({ ...newTask, description: e.target.value })}
                    placeholder="Enter task description"
                  />
                </div>
                <div className="grid gap-2">
                  <Label htmlFor="priority">Priority</Label>
                  <Select 
                    value={newTask.priority} 
                    onValueChange={(value) => setNewTask({ ...newTask, priority: value })}
                  >
                    <SelectTrigger>
                      <SelectValue placeholder="Select priority" />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="High">High</SelectItem>
                      <SelectItem value="Medium">Medium</SelectItem>
                      <SelectItem value="Low">Low</SelectItem>
                    </SelectContent>
                  </Select>
                </div>
                <div className="grid gap-2">
                  <Label htmlFor="assignee">Assignee</Label>
                  <Input
                    id="assignee"
                    value={newTask.assignee}
                    onChange={(e) => setNewTask({ ...newTask, assignee: e.target.value })}
                    placeholder="Enter assignee name"
                  />
                </div>
                <div className="grid gap-2">
                  <Label htmlFor="dueDate">Due Date</Label>
                  <Input
                    id="dueDate"
                    type="date"
                    value={newTask.dueDate}
                    onChange={(e) => setNewTask({ ...newTask, dueDate: e.target.value })}
                  />
                </div>
              </div>
              <div className="flex justify-end space-x-2">
                <Button variant="outline" onClick={() => setShowNewTaskDialog(false)}>
                  Cancel
                </Button>
                <Button onClick={handleCreateTask} className="bg-purple-600 hover:bg-purple-700">
                  Create Task
                </Button>
              </div>
            </DialogContent>
          </Dialog>
        </div>

        {/* Stats Cards */}
        <div className="grid grid-cols-1 md:grid-cols-4 gap-6 mb-6">
          <Card>
            <CardHeader className="pb-3">
              <CardTitle className="text-sm font-medium text-gray-600">Total Team Members</CardTitle>
              <CardDescription className="text-2xl font-bold text-gray-900">{teamStats.totalMembers}</CardDescription>
            </CardHeader>
          </Card>
          <Card>
            <CardHeader className="pb-3">
              <CardTitle className="text-sm font-medium text-gray-600">Active Projects</CardTitle>
              <CardDescription className="text-2xl font-bold text-gray-900">{teamStats.activeProjects}</CardDescription>
            </CardHeader>
          </Card>
          <Card>
            <CardHeader className="pb-3">
              <CardTitle className="text-sm font-medium text-gray-600">Completed Tasks</CardTitle>
              <CardDescription className="text-2xl font-bold text-gray-900">{teamStats.completedTasks}</CardDescription>
            </CardHeader>
          </Card>
          <Card>
            <CardHeader className="pb-3">
              <CardTitle className="text-sm font-medium text-gray-600">Pending Tasks</CardTitle>
              <CardDescription className="text-2xl font-bold text-gray-900">{teamStats.pendingTasks}</CardDescription>
            </CardHeader>
          </Card>
        </div>

        {/* Tasks Section */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
          {/* Pending Tasks */}
          <Card>
            <CardHeader>
              <CardTitle>Pending Tasks</CardTitle>
              <CardDescription>Tasks that need your attention</CardDescription>
            </CardHeader>
            <CardContent>
              <div className="space-y-3">
                {tasks.filter(task => task.status === "pending").map((task) => (
                  <div key={task.id} className="flex items-center space-x-3 p-3 border rounded-lg">
                    <Checkbox 
                      checked={false}
                      onCheckedChange={() => toggleTaskStatus(task.id)}
                    />
                    <div className="flex-1">
                      <p className="font-medium">{task.title}</p>
                      <p className="text-sm text-gray-600">Assigned to: {task.assignee}</p>
                    </div>
                    <Badge className={getPriorityColor(task.priority)}>{task.priority}</Badge>
                    <span className="text-sm text-gray-500">{task.dueDate}</span>
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>

          {/* Completed Tasks */}
          <Card>
            <CardHeader>
              <CardTitle>Recently Completed</CardTitle>
              <CardDescription>Tasks you've finished recently</CardDescription>
            </CardHeader>
            <CardContent>
              <div className="space-y-3">
                {tasks.filter(task => task.status === "completed").slice(0, 5).map((task) => (
                  <div key={task.id} className="flex items-center space-x-3 p-3 border rounded-lg opacity-75">
                    <Checkbox 
                      checked={true}
                      onCheckedChange={() => toggleTaskStatus(task.id)}
                    />
                    <div className="flex-1">
                      <p className="font-medium line-through">{task.title}</p>
                      <p className="text-sm text-gray-600">Completed by: {task.assignee}</p>
                    </div>
                    <Badge className={getPriorityColor(task.priority)}>{task.priority}</Badge>
                    <span className="text-sm text-gray-500">{task.dueDate}</span>
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  );
}
